@import url(./header.css);

/* Reset styles */
:root {
    --light-beige: #F6F5F1;
    --dark-brown: #210803;
    --brown: #2D1B0D;
    --accent: #B31719;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
}
p:last-child {
    margin-bottom: 0;
}
ul {
    margin: 0;
    padding: 0;
    list-style: none;
}
a {
    color: currentColor;
    text-decoration: none;
}
button {
    cursor: pointer;
}
img {
    display: block;
    max-width: 100%;
    height: auto;
}
body {
    font-family: "Helvetica Neue", sans-serif;
    color: var(--dark-brown);
    background-color: var(--light-beige);
}
.no-scroll {
    overflow: hidden;
}
.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}