.header {
  width: 100%;
  margin: 0 auto;
  display: flex;
}

.container {
  width: 100%;
  margin: 0 auto;
}

.header-container {
  justify-content: space-between;
  align-items: center;
  padding: 24px 16px;
  display: flex;
}

.header-menu-btn {
  width: 24px;
  height: 24px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
}

.header-menu-pic {
  stroke: var(--brown);
}

.header-menu-pic:hover, .header-menu-pic:focus {
  stroke: var(--accent);
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }

  .header-container {
    padding: 32px 50px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    width: 1440px;
  }

  .header-container {
    padding: 48px 80px 40px;
  }
}

:root {
  --light-beige: #f6f5f1;
  --dark-brown: #210803;
  --brown: #2d1b0d;
  --accent: #b31719;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

body {
  color: var(--dark-brown);
  background-color: var(--light-beige);
  font-family: Helvetica Neue, sans-serif;
}

.no-scroll {
  overflow: hidden;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

